@import '../../common/colors.scss';

.card-item{
    background: $secondary-color;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.3s;
    min-height: 450px;
    height: 100%;
    margin: 10px;

    &:hover{
        transform: scale(1.05);
        transition: all 0.3s;
    }
}

.card-top{
    height: 300px;

    img{
        border-top-left-radius:10px ;
        border-top-right-radius:10px ;
        width: 100%;
        height: 100%;
    }
}

.card-bottom{
    padding: 20px;
}

.card-info {
    color:$font-primary;
    h4{
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 10px;
    }
}