@import "../../common/colors.scss";

.movie-section{
    display: flex;
    justify-content: space-evenly;
    padding: 40pxx 0px;
    color: $font-primary;
    font-weight: 400;
}

.movie-rating{
    padding-left:3px;
    margin-top: 20px;
    color: $font-secondary;
    display: flex;

    span {
        margin-right:20px;
    }
}

.movie-title {
    font-size: 40px;
    color: $font-primary;
}

.movie-plot{
    margin-top: 20px;
    line-height: 1.8rem;
}

.movie-info > div {
    padding: 10px 0px;
}

.movie-info > div span:first-child {
    padding: 10px;
    color: $font-primary;
    font-weight: 600;
    width: 100px;
    display: inline;
}

.movie-info > div span{
    color: $font-secondary;
}

.fa-star {
    color: #ff9c00;
}

.fa-thumbs-up {
    color: #fafafa;
}
.fa-film {
    color: rgb(191,213,214);
}
.fa-calendar {
    color: peachpuff;
}

.section-right {
    margin-left: 30px;
}